<template>
  <div id="app">
    <Navbar></Navbar>
    <router-view/>
    <Footer></Footer>
  </div>
</template>

<script>
import Navbar from '@/components/nav/Navbar.vue'
import Footer from '@/components/Footer.vue'

export default {
  components: {
    Navbar,
    Footer
  },
  watch: {
    '$route': {
      handler: (to) => {
        if (to.meta.title) {
          document.title = 'Abbott Paint | ' + to.meta.title
        } else {
					document.title = 'Abbott Paint'
				}
      },
      immediate: true
    }
  }
}
</script>
